html {
 box-sizing:border-box
}
*,
:after,
:before {
 box-sizing:inherit
}
::-moz-selection {
 background-color:#000;
 color:#fff
}
::-o-selection {
 background-color:#000;
 color:#fff
}
::selection {
 background-color:#000;
 color:#fff
}
body {
 color:#ebebeb;
 background:#182d4f;
 font-size:100%;
 font-family:Lato,"Helvetica Neue",Helvetica,Arial,sans-serif;
 font-weight:400;
 text-align:center;
 margin:0;
 padding:2em .5em;
 height:100vh;
 line-height:1.42857143
}
a {
 color:#ebebeb;
 text-decoration:none
}
img {
 width:100%;
 max-width:100%;
 vertical-align:middle
}
ul {
 padding-left:0;
 list-style:none;
 max-width:29.375em;
 margin:0 auto;
 display:grid;
 grid-row-gap:1.125em
}
ul a,
ul li {
 display:inline-block
}
.strong,
strong {
 color:#f9c029;
 font-weight:400
}
li:nth-child(5n+1) article {
 background:#3b9ae1
}
li:nth-child(5n+2) article {
 background:#f6be00
}
li:nth-child(5n+3) article {
 background:#e64134
}
li:nth-child(5n+4) article {
 background:#eb70b1
}
li:nth-child(5n+5) article {
 background:#0f1a5f
}
.dude {
 display:grid;
 grid-template-columns:1fr 20fr 1fr;
 justify-items:start;
 align-items:center
}
article {
 color:#fff;
 font-size:.875em;
 font-weight:700;
 background:#9f3fa3;
 border:.25em solid transparent;
 border-radius:.5625em;
 width:100%;
 padding:.625em .5em;
 transition:.15s border-color ease-in-out,.15s opacity ease-in-out
}
article strong {
 color:#fff;
 font-weight:300
}
article span {
 font-weight:400;
 opacity:.75;
 display:block
}
.faded {
 opacity:.35
}
.gold {
 border-color:#ff0
}
.preview {
 font-weight:300;
 font-size:.8125em;
 opacity:.5
}
.ctrl {
 color:#fff;
 font-size:.75em;
 font-weight:700;
 font-family:inherit;
 cursor:pointer;
 text-decoration:none;
 width:4.25em;
 padding:.75em 1.5em .75em 0;
 text-align:right
}
input.ctrl {
 border:none;
 background-color:transparent;
 padding-left:1.5em;
 padding-right:0;
 text-align:left
}
.add-new {
 max-width:18.75em;
 margin:2em auto 3em
}
.add-new input {
 color:#fff;
 font-size:.875em;
 background:0 0;
 border:none;
 border-bottom:.125em solid rgba(255,255,255,.9);
 display:block;
 margin:0 auto .5em;
 text-align:center
}
.add-new input:focus {
 outline:0
}
.add-new input:first-child {
 font-weight:700;
 max-width:100%
}
.add-new input:last-child {
 font-size:.8125em;
 width:100%
}
@media (max-width:480px) {
 article {
  font-size:.75em
 }
}
.dude-enter-active,
.dude-exit-active {
   transition: all 0.2s;
}

.dude-enter {
   opacity: 0;
   transform: scale(0.75);
}

.dude-enter-active {
   opacity: 1;
   transform: translateX(0);
}

.dude-exit {
   opacity: 1;
}

.dude-exit-active {
   opacity: 0;
   transform: scale(0.75);
}
